<template>
  <div>
    <section class="w3l-coming-soon-page">
      <div class="coming-page-info-6">
        <div class="wrapper">
          <div class="coming-block">
            <h1>Coming <br>Soon</h1>
            <p>There will be something Very Awesome</p>
            <div class="countdown">
              <div class="countdown__days">
                <div class="number">{{nyr.day}}</div>
                <span class>Days</span>
              </div>

              <div class="countdown__hours">
                <div class="number">{{nyr.hour}}</div>
                <span class>Hours</span>
              </div>

              <div class="countdown__minutes">
                <div class="number">{{nyr.minute}}</div>
                <span class>Minutes</span>
              </div>

              <div class="countdown__seconds">
                <div class="number">{{nyr.second}}</div>
                <span class>Seconds</span>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import "../styles/lovqStyle.css";
export default {
  data() {
    return {
      now: new Date(),
      // 从她回复我的第一句话在我接到的时间算起
      testDay: new Date("2020-11-24 22:22:12"),
      nyr: "",
    };
  },
  computed: {
    nyrc() {
      return this.GetDetailDate(this.testDay, new Date());
    },
  },
  mounted: function () {
    this.nyr = this.GetDetailDate(this.testDay, new Date());
    setInterval(() => {
      this.nyr = this.GetDetailDate(this.testDay, new Date());
    }, 1000);
  },
  methods: {
    /**
     * 获取相差的天数
     * @dateStart 开始的日期
     * @dateEnd 结束的日期
     */
    GetDifferenceDays(dateStart, dateEnd) {
      var diffTime =
        parseInt(dateEnd.getTime() / 1000) - dateStart.getTime() / 1000;
      var timeDay = parseInt(diffTime / 60 / 60 / 24); //相差天数
      return timeDay;
    },
    /**
     * 获取相差的天数(不考虑小时)
     * @dateStart 开始的日期
     * @dateEnd 结束的日期
     */
    GetDifferenceDaysIgnore(dateStart, dateEnd) {
      dateStart = this.GetYMD(dateStart);
      dateEnd = this.GetYMD(dateEnd);
      var diffTime =
        parseInt(dateEnd.getTime() / 1000) - dateStart.getTime() / 1000;
      var timeDay = parseInt(diffTime / 60 / 60 / 24); //相差天数
      return timeDay;
    },
    /**
     * 获取相差的小时数
     * @dateStart 开始的日期
     * @dateEnd 结束的日期
     */
    GetDifferenceHours(dateStart, dateEnd) {
      var diffTime =
        parseInt(dateEnd.getTime() / 1000) - dateStart.getTime() / 1000;
      var timeHour = parseInt(diffTime / 60 / 60); //相差小时
      return timeHour;
    },
    /**
     * 获取相差的分钟数
     * @dateStart 开始的日期
     * @dateEnd 结束的日期
     */
    GetDifferenceMinutes(dateStart, dateEnd) {
      var diffTime =
        parseInt(dateEnd.getTime() / 1000) - dateStart.getTime() / 1000;
      var timeMinutes = parseInt(diffTime / 60); //相差分钟
      return timeMinutes;
    },
    /**
     * 获取一个 天-时-分-秒 格式的json串
     * @dateStart 开始的日期
     * @dateEnd 结束的日期
     */
    GetDetailDate(dateStart, dateEnd) {
      var dayT = this.GetDifferenceDays(dateStart, dateEnd);

      // 某日那天的秒数
      var endS =
        dateEnd.getHours() * 60 * 60 +
        dateEnd.getMinutes() * 60 +
        dateEnd.getSeconds();
      var startS =
        dateStart.getHours() * 60 * 60 +
        dateStart.getMinutes() * 60 +
        dateStart.getSeconds();

      endS = endS > startS ? endS : endS + 24 * 60 * 60;

      var secondT = (endS - startS) % 60;
      var minuteT = parseInt((endS - startS - secondT) / 60) % 60;
      var hourT = parseInt((endS - startS - secondT) / 60 / 60) % 24;

      var dateText = {
        day: dayT,
        hour: hourT,
        minute: minuteT,
        second: secondT,
      };
      return dateText;
    },
    /**
     * 去除一个日期的时分秒 即获取某个日期的 0:0:0 的时间对象
     * @date 日期对象
     */
    GetYMD(date) {
      if (date) {
        return new Date(date.getFullYear(), date.getMonth(), date.getDate());
      }
    },
    /**
     * 获取今年的最后一天的时间对象
     */
    GetLastDayDate() {
      return new Date(new Date().getFullYear() + "-12-31");
    },
    /**
     * 获取日期所在年份的第几天
     */
    GetDayIndex(date) {
      return (
        Math.ceil(
          (date - new Date(date.getFullYear().toString())) /
            (24 * 60 * 60 * 1000)
        ) + 1
      );
    },
  },
};
</script>

