import { createWebHashHistory, createRouter } from 'vue-router';
import Home from '../views/index.vue'
import Lovq from '../views/Lovq.vue'
import Emoticon from '../views/emoticon/index.vue';

// createWebHistory
const history = createWebHashHistory();
const router = createRouter({
    history,
    routes: [
        {
            path: '/',
            component: Home,
            meta: {
                title: '首页',
                keepAlive: false
            }
        },
        {
            path: '/lovq',
            component: Lovq,
            meta: {
                title: '计时器页面',
                keepAlive: false
            }
        },
        {
            path: '/emoticon',
            component: Emoticon,
            meta: {
                title: '记仇表情包生成器',
                keepAlive: false
            }
        },
    ]
});

export default router;