<template>
  <div class="mainPanel">
    <a-card class="mainCard" style="margin: 0 auto;">
      <a-row type="flex" justify="center">
        <a-col>
          <img :src="imgSrcc" alt="图片" width="300" />
        </a-col>
      </a-row>
      <br />
      <a-row>
        <a-col class="labletext" :span="4">天气</a-col>
        <a-col :span="20">
          <a-select default-value="晴" @change="weatherChange" style="width: 100%">
            <a-select-option value="晴">晴</a-select-option>
            <a-select-option value="多云">多云</a-select-option>
            <a-select-option value="阴">阴</a-select-option>
            <a-select-option value="小雨">小雨</a-select-option>
            <a-select-option value="大雨">大雨</a-select-option>
            <a-select-option value="暴雨">暴雨</a-select-option>
            <a-select-option value="雪">雪</a-select-option>
          </a-select>
        </a-col>
      </a-row>
      <br />
      <a-row>
        <a-col class="labletext" :span="4" style="text-align: left;">主要文本</a-col>
        <a-col :span="20">
          <a-textarea @blur="textChange($event,index,1)" placeholder="请输入..." :auto-size="{ minRows: 3, maxRows: 5 }" />
        </a-col>
      </a-row>
      <br />
      <a-row type="flex" justify="center">
        <a-col :span="10">
          <a-button class="space" type="primary" @click="emoticonGenerate" block>
            <DoubleRightOutlined />生成
            <DoubleLeftOutlined />
          </a-button>
        </a-col>
      </a-row>
    </a-card>
  </div>
</template>

<script>
import request from "@/utils/request";
import qs from "qs";
import { DoubleRightOutlined, DoubleLeftOutlined } from "@ant-design/icons-vue";
export default {
  components: { DoubleRightOutlined, DoubleLeftOutlined },
  data() {
    return {
      weather: "晴",
      text: "",
      imgSrcc:
        "https://wpimg.wallstcn.com/007ef517-bafd-4066-aae4-6883632d9646",
    };
  },
  computed: {},
  methods: {
    weatherChange(value) {
      this.weather = value;
    },
    textChange(e) {
      this.text = e.target.value;
    },
    emoticonGenerate() {
      var data = {
        Date: "",
        Weather: this.weather,
        Text: this.text,
      };
      request
        .post("Emoticon/EmoticonGenerate", qs.stringify(data))
        .then((response) => {
          this.imgSrcc = response.data.imageSrc;
        })
        .catch(function (error) {
          console.log(error);
        });
    },
  },
};
</script>

<style>
.space {
  margin: 10px;
}
.labletext {
  padding: 0 5px;
}
.mainPanel {
  padding: 30px;
}
.mainCard {
  width: 100%;
}
@media (min-width: 1200px) {
  .mainCard {
    width: 1000px;
  }
}
</style>